html {
  height: 100%; }

body {
  width: 100%;
  min-width: 1280px;
  background-color: #ffffff;
  margin: 0 auto;
  text-align: center;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-style: normal;
  color: #212529;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  position: relative; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%); }

.up-button {
  position: fixed;
  right: 100px;
  bottom: 30px;
  text-align: center;
  cursor: pointer; }
  .up-button:hover a {
    position: relative;
    color: #2d80fc;
    background-image: url("../img/arrow_up-hover-icon.svg"); }
  .up-button:hover::before {
    position: absolute;
    top: 0;
    left: 6px;
    display: block;
    content: "";
    width: 48px;
    height: 48px;
    border-radius: 50%;
    box-shadow: 0px 10px 16px rgba(0, 0, 120, 0.1); }
  .up-button a {
    text-decoration: none;
    margin: 0;
    font-size: 14px;
    line-height: 17px;
    font-weight: bold;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #9999c9;
    padding-top: 60px;
    display: block;
    width: 60px;
    height: 77px;
    background-image: url("../img/arrow_up-icon.svg");
    background-repeat: no-repeat;
    background-size: 48px 48px;
    background-position: top center; }

.up-button-hide {
  display: none; }

.networks {
  position: fixed;
  z-index: 100;
  right: 6px;
  top: 180px; }

.networks__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.networks__item {
  width: 36px;
  margin: 0 0 5px; }

.networks__item-link svg {
  width: 36px;
  height: 36px;
  fill: #000078; }

.networks__item-link svg:hover {
  fill: #2d80fc; }

.button {
  text-align: center;
  display: block;
  box-sizing: border-box;
  padding: 20px 20px;
  width: fit-content;
  font-size: 14px;
  line-height: 17px;
  font-weight: bold;
  color: #ffffff;
  background-color: #000078;
  border-radius: 8px;
  text-decoration: none;
  text-transform: uppercase; }
  .button:hover, .button:focus {
    background-color: #2d80fc;
    box-shadow: 0 10px 16px rgba(45, 128, 252, 0.2); }

.no-scroll {
  overflow: hidden; }

.page-wrapper {
  width: 100vw; }

a, input {
  outline: none; }

@media (max-width: 1279px) {
  body {
    min-width: 640px; }
