.tab__press {
  width: 800px;
  text-align: left;

  h1 {
    @include title-h1;
  }
}

.tab__press-list {
  @include list;
  margin: 40px 0 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 1242px;
  overflow: hidden;
}

.tab__press-item {
  width: 48.5%;
  flex-basis: 48.5%;
  margin: 0 0 24px;
  border: 1px solid $button-second;
  transition: box-shadow 0.5s;
  position: relative;
  z-index: 10;

  &::after {
    display: block;
    content: "";
    width: 53px;
    height: 100%;
    background-image: url("../img/pattern.svg");
    position: absolute;
    z-index: -10;
    top: 0;
    right: 0;
    opacity: 0;
    transition: opacity 0.5s;
  }

  &:hover {
    box-shadow: 0px 24px 20px -10px rgba(0, 0, 120, 0.1);
    transition: box-shadow 0.5s;

    .tab__press-item-title {
      time {
        color: $blue;
      }

      h4 {
        color: $blue;
      }
    }

    &::after {
      opacity: 1;
      transition: opacity 0.5s;
    }
  }
}

.tab__press-item-link {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 388px;
  padding: 32px;
  text-decoration: none;
}

.tab__press-item-title {
  time {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: $black-text;
  }

  h4 {
    @include title-h4;
    color: $black-text;
    line-height: 30px;
    margin: 16px 0 0;
  }
}

.tab__press-item-photos-list {
  @include list;
  display: flex;
  justify-content: flex-start;

}

.tab__press-item-photos-item {
  margin: 0 22px 0 0;
}

.tab__press-item-photos-item-pic {
  width: 64px;
  height: 64px;
}

.tab__press-list-navigation {
  margin: 56px auto 0;
  display: flex;
  justify-content: space-between;
  width: 480px;
}

.tab__press-list-prev,
.tab__press-list-next {
  width: 48px;
  height: 48px;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  cursor: pointer;
  outline: none;
}

.tab__press-list-prev {
  background-image: url("../img/arrow_left-icon.svg");

  &:hover {
    background-image: url("../img/arrow_left-hover-icon.svg");
  }
}

.tab__press-list-next {
  background-image: url("../img/arrow_right-icon.svg");

  &:hover {
    background-image: url("../img/arrow_right-hover-icon.svg");
  }
}

.tab__press-list-pagination {
  width: 304px;
  display: flex;
  justify-content: space-between;
  @include list;
  counter-reset: customCounter;
}

.tab__press-list-pagination-bullet {
  width: 48px;
  height: 48px;
  border: 1px solid $button-second;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  cursor: pointer;
  outline: none;

  &::before {
    display: block;
    counter-increment: customCounter;
    content: counter(customCounter);

    font-family: $Stratos;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    text-align: center;
    color: $blue;
    padding: 12px 0;
  }

  &:hover {
    border: 1px solid $hover-text;
    background-color: $hover-text;

    &::before {
      color: $white;
    }
  }
}

.tab__press-list-pagination-bullet-active {
  border: 1px solid $blue;
  background-color: $blue;

  &::before {
    color: $white;
  }
}

.tab__press-form {
  margin: 40px 0 0;
  width: 100%;
  background-color: $background-footer;
  border-radius: 8px;
  box-sizing: border-box;
}

.tab__info-document-list-wrapper-toggle {
  display: none;
}

.tab__search {
  display: flex;
  justify-content: space-between;
  padding: 32px;
  background-image: url("../img/pattern.svg");

  & > h1 {
    display: none;
  }
}

.tab__search-input {
  position: relative;

  input {
    border: 1px solid $button-second;
    box-sizing: border-box;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
    padding: 17px 0 17px 15px;
    height: 56px;
    width: 166px;
    box-shadow: none;

    & + label {
      font-size: 14px;
      line-height: 20px;
      color: $black-text;
    }

    &:focus,
    &:hover {
      border: 1px solid $light-gray;
      filter: drop-shadow(0 5px 10px rgba(0, 0, 120, 0.05));
    }
  }

  label {
    position: absolute;
    top: 18px;
    left: 19px;
    pointer-events: none;
    transition: 0.3s;
  }

  &--region {
    input {
      width: 356px;
    }
  }

  & input:focus ~ label,
  & input:not(:focus):valid ~ label {
    top: 8px;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: $light-gray;
  }
}

.tab__form-button {
  display: none;
}

.tab__search-toggle {
  display: none;
}

@media (max-width: $tablet-max-width) {
  .tab__press {
    width: 100%;
    max-width: 800px;
    box-sizing: border-box;
    padding: 0 32px;
    margin: 0 auto;

    h1 {
      @include title-tablet-h1;
      margin: 40px 0 0;
    }
  }

  .tab__press-form {
    margin: 32px 0 0;
  }

  .tab__search-input {

    input {
      width: 100%;

      & + label {
        font-size: 14px;
        line-height: 20px;
        color: $black-text;
      }

      &:focus,
      &:hover {
        border: 1px solid $light-gray;
        filter: drop-shadow(0 5px 10px rgba(0, 0, 120, 0.05));
      }
    }

    label {
      position: absolute;
      top: 18px;
      left: 19px;
      pointer-events: none;
      transition: 0.3s;
    }

    &--region {
      margin: 0 24px 0 0;

      input {
        max-width: 356px;
        min-width: 219px;
      }
    }

    &--year {
      margin: 0 24px 0 0;

      input {
        max-width: 166px;
        min-width: 112px;
      }
    }

    &--month {
      input {
        max-width: 166px;
        min-width: 137px;
      }
    }

    & input:focus ~ label,
    & input:not(:focus):valid ~ label {
      top: 8px;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.02em;
      color: $light-gray;
    }
  }

  .tab__press-list {
    margin: 24px 0 0;
    height: 1134px;
  }

  .tab__press-item-link {
    padding: 32px 28px 32px 32px;
    height: 360px;
  }

  .tab__press-item-title {

    time {
      line-height: 19px;
    }

    h4 {
      @include title-tablet-h2;
      margin: 11px 0 0;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .tab__press {
    max-width: $mobile-max-width;
    padding: 0 24px;
  }

  .tab__press-item {
    width: 100%;
    flex-basis: 100%;
  }

  .tab__press-list-navigation {
    margin: 32px auto 0;
    width: 272px;
  }

  .tab__press-list-pagination {
    width: 160px;
  }

  .tab__press-list-pagination-bullet {
    &:nth-child(4),
    &:nth-child(5) {
      display: none;
    }
  }

  .tab__form-button {
    cursor: pointer;
    border: none;
    outline: none;
    width: 100%;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 17px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 0.03em;
    color: $blue;
    box-sizing: border-box;
    padding: 16px 0;
    border-radius: 8px;
    vertical-align: middle;
    display: flex;
    justify-content: center;
    align-items: center;

    svg {
      width: 24px;
      height: 24px;
      margin: 0 10px 0 0;
    }

    span {
      display: block;
    }
  }

  .tab__press-form {
    margin: 40px 0 0;
    width: 100%;
    background-color: $background-footer;
    border-radius: 8px;
    box-sizing: border-box;
  }

  .tab__search-toggle {
    display: block;
    width: 32px;
    height: 32px;
    background-image: url("../img/menu-close-icon.svg");
    background-color: transparent;
    border: none;
    margin: 0 0 0 auto;
    cursor: pointer;
    outline: none;


    &:hover {
      background-image: url("../img/menu-hover-close-icon.svg");
      outline: none;
    }
  }

  .tab__search {
    padding: 24px;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-image: none;
    background-color: $white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    box-sizing: border-box;

    & > h1 {
      display: block;
      @include title-tablet-h1;
      margin: 0 0 32px 0;
    }

    &--hide {
      display: none;
    }
  }

  .tab__search-input {
    input {
      & + label {
        font-size: 14px;
        line-height: 20px;
        color: $black-text;
      }

      &:focus,
      &:hover {
        border: 1px solid $light-gray;
        filter: drop-shadow(0 5px 10px rgba(0, 0, 120, 0.05));
      }
    }

    label {
      position: absolute;
      top: 18px;
      left: 19px;
      pointer-events: none;
      transition: 0.3s;
    }

    &--region {
      margin: 0 0 24px 0;

      input {
        max-width: 591px;
        min-width: 272px;
      }
    }

    &--year {
      margin: 0 0 24px 0;

      input {
        max-width: 591px;
        min-width: 272px;
      }
    }

    &--month {
      input {
        max-width: 591px;
        min-width: 272px;
      }
    }
  }
}
