.tab__article {
  width: 800px;
  text-align: left;

  h1 {
    @include title-h1;
  }

  h2 {
    @include title-h2;
    margin: 40px 0 0;
  }

  span {
    font-size: 16px;
    line-height: 24px;
    color: $light-gray;
    display: block;
    margin: 24px 0 0;
  }
}

.tab__article-description {
  font-size: 16px;
  line-height: 24px;
  width: 697px;
  margin: 0 0 0 auto;
}

.tab__article-photo {
  display: flex;
  justify-content: flex-start;
  width: 697px;
  margin: 0 0 0 auto;
  overflow: auto;

  picture {
    margin: 0 4px 0 0;
  }
}

@media (max-width: $tablet-max-width) {
  .tab__article {
    width: 100%;
    max-width: 800px;
    box-sizing: border-box;
    padding: 0 32px;
    margin: 0 auto;

    h1 {
      @include title-tablet-h1;
      margin: 40px 0 0;
    }

    h2 {
      @include title-tablet-h2;
      margin: 32px 0 0;
    }

    span {
      font-size: 14px;
      line-height: 20px;
      margin: 14px 0 0;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .tab__article {
    max-width: $mobile-max-width;
    padding: 0 24px;

    h1 {
      margin: 24px 0 0;
    }

    h2 {
      margin: 24px 0 0;
    }
  }
}
