.tab__honor {
  width: 800px;
  text-align: left;

  h1 {
    @include title-h1;
  }
}

.tab__honor-list {
  @include list;
  margin: 37px 0 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 1242px;
  overflow: hidden;
}

.tab__honor-item {
  width: 48.5%;
  flex-basis: 48.5%;
  margin: 0 0 24px;
  border: 1px solid $button-second;
  transition: box-shadow 0.5s;
  position: relative;
  z-index: 10;

  &::after {
    display: block;
    content: "";
    width: 53px;
    height: 100%;
    background-image: url("../img/pattern.svg");
    position: absolute;
    z-index: -10;
    top: 0;
    right: 0;
    opacity: 0;
    transition: opacity 0.5s;
  }

  &:hover {
    box-shadow: 0px 24px 20px -10px rgba(0, 0, 120, 0.1);
    transition: box-shadow 0.5s;

    .tab__honor-item-title {
      time {
        color: $blue;
      }

      h4 {
        color: $blue;
      }
    }

    &::after {
      opacity: 1;
      transition: opacity 0.5s;
    }
  }
}

.tab__honor-item-link {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 388px;
  padding: 32px;
  text-decoration: none;
}

.tab__honor-item-title {
  time {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
    color: $black-text;
  }

  h4 {
    @include title-h4;
    color: $black-text;
    line-height: 30px;
    margin: 16px 0 0;
  }
}

.tab__honor-item-photos-list {
  @include list;
  display: flex;
  justify-content: flex-start;

}

.tab__honor-item-photos-item {
  margin: 0 22px 0 0;
}

.tab__honor-item-photos-item-pic {
  width: 64px;
  height: 64px;
}

.tab__honor-list-navigation {
  margin: 56px auto 0;
  display: flex;
  justify-content: space-between;
  width: 480px;
}

.tab__honor-list-prev,
.tab__honor-list-next {
  width: 48px;
  height: 48px;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  cursor: pointer;
  outline: none;
}

.tab__honor-list-prev {
  background-image: url("../img/arrow_left-icon.svg");

  &:hover {
    background-image: url("../img/arrow_left-hover-icon.svg");
  }
}

.tab__honor-list-next {
  background-image: url("../img/arrow_right-icon.svg");

  &:hover {
    background-image: url("../img/arrow_right-hover-icon.svg");
  }
}

.tab__honor-list-pagination {
  width: 304px;
  display: flex;
  justify-content: space-between;
  @include list;
  counter-reset: customCounter;
}

.tab__honor-list-pagination-bullet {
  width: 48px;
  height: 48px;
  border: 1px solid $button-second;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  cursor: pointer;
  outline: none;

  &:before {
    display: block;
    counter-increment: customCounter;
    content: counter(customCounter);

    font-family: $Stratos;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    text-align: center;
    color: $blue;
    padding: 12px 0;
  }

  &:hover {
    border: 1px solid $hover-text;
    background-color: $hover-text;

    &:before {
      color: $white;
    }
  }
}

.tab__honor-list-pagination-bullet-active {
  border: 1px solid $blue;
  background-color: $blue;

  &:before {
    color: $white;
  }
}

@media (max-width: $tablet-max-width) {
  .tab__honor {
    width: 100%;
    max-width: 800px;
    box-sizing: border-box;
    padding: 0 32px;
    margin: 0 auto;

    h1 {
      @include title-tablet-h1;
      margin: 32px 0 0;
    }
  }

  .tab__honor-list {
    margin: 24px 0 0;
    height: 1134px;
  }

  .tab__honor-item-link {
    padding: 32px 28px 32px 32px;
    height: 360px;
  }

  .tab__honor-item-title {

    time {
      line-height: 19px;
    }

    h4 {
      @include title-tablet-h2;
      margin: 11px 0 0;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .tab__honor {
    max-width: $mobile-max-width;
    padding: 0 24px;
  }

  .tab__honor-item {
    width: 100%;
    flex-basis: 100%;
  }

  .tab__honor-list-navigation {
    margin: 32px auto 0;
    width: 272px;
  }

  .tab__honor-list-pagination {
    width: 160px;
  }

  .tab__honor-list-pagination-bullet {
    &:nth-child(4),
    &:nth-child(5) {
      display: none;
    }
  }

}
