.page-header {
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.page-header__version-toggle {
  background-color: $blue;
  margin: 0 auto;
  padding: 2px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  text-decoration: none;

  &:hover,
  &:focus {
    background-color: $hover-text;

    & > .page-header__version-toggle-icon {
      svg {
        fill: $hover-text;
      }
    }
  }
}

.page-header__version-toggle-icon {
  width: 32px;
  margin: 7px 10px 0 0;

  svg {
    width: 32px;
    height: 32px;
    fill: $blue;
  }
}

.main-nav__wrapper {
  width: 100%;
  box-sizing: border-box;
  padding-bottom: 219px;
  background-image: url("../img/company-header-background_desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &--news {
    background-image: url("../img/news-header-background_desktop@1x.jpg");
  }
}

.main-nav {
  width: 100%;
  max-width: 1212px;
  margin: 0 auto;
  padding: 28px 0;
  display: flex;
  justify-content: space-between;
}

.main-nav__menu,
.main-nav__help-list,
.main-nav__help-item-description {
  display: flex;
  justify-content: left;
  align-items: center;
}

.main-nav__logo {
  width: 234px;
  text-decoration: none;
  margin: 4px 64px 0 0;

  svg {
    width: 234px;
    height: 28px;
    fill: $blue;
  }

  &--white {
    svg {
      fill: $white;
    }
  }

  &:hover {
    svg {
      fill: $hover-text;
    }
  }
}

.main-nav__toggle {
  width: 32px;
  height: 32px;
  background-image: url("../img/menu-icon.svg");
  background-color: transparent;
  border: none;
  margin: 0 33px 0 0;
  cursor: pointer;
  outline: none;

  &:hover {
    background-image: url("../img/menu-hover-icon.svg");
    outline: none;
  }

  &--white {
    background-image: url("../img/menu-icon-white.svg");
  }
}

.main-nav__city {
  color: $blue;
  text-decoration: none;

  &:hover,
  &:focus {
    color: $hover-text;
  }

  &--white {
    color: $white;
  }
}

.main-nav__help-list {
  width: 504px;
  list-style: none;
  margin: 0 5px 0 0;
  padding: 0;
  justify-content: flex-end;

  &--company {
    margin: 0;
  }
}

.main-nav__help-item {
  text-align: left;

  a {
    text-decoration: none;
  }

  &--address {
    margin: 0 30px 0 0;
  }

  &--account {
    margin: 0 24px 0 0;
  }

  &--tel {
    margin: 0 26px 0 0;

    a {
      color: $blue;
      width: 104px;
    }
  }

  &--feedback {
    a {
      color: $blue;
      width: 94px;
    }
  }

  &--white {
    a {
      color: $white;
    }
  }

  &:last-child {
    margin: 0;
  }

  & a:hover,
  & a:focus {
    color: $hover-text;

    .main-nav__help-item-icon {
      svg {
        fill: $hover-text;
      }
    }
  }
}

.main-nav__help-item-description {
  &--address {
    width: 95px;
  }

  width: 134px;
  margin: 0;
  color: $blue;
  text-decoration: none;
}

.main-nav__help-item-button {
  color: $white;
  width: 230px;

  &:hover,
  &:focus {
    outline: none;
    background-color: $hover-text;
  }

}

.main-nav__help-item-icon {
  width: 32px;
  margin: 3px 10px 0 0;

  svg {
    width: 32px;
    height: 32px;
    fill: $blue;
  }
}

.main-nav-menu {
  width: 100%;
  max-width: 1212px;
  margin: 0 auto;
  padding: 18px 0 22px;
  display: flex;
  justify-content: space-between;
