@media (max-width: $tablet-max-width) {
  .page-header__version-toggle {
    padding: 0;

    & span {
      margin: 10px 0;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
  }

  .page-header__version-toggle-icon {
    width: 24px;
    margin: 4px 8px 0 0;

    svg {
      width: 24px;
      height: 24px;
    }

  }

  .main-nav {
    padding: 16px 26px 0 32px;
    box-sizing: border-box;
  }

  .main-nav__wrapper {
    width: 100%;
    padding-bottom: 9%;
    background-image: url("../img/company-header-background_desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .main-nav__menu {
    justify-content: space-between;
  }

  .main-nav-menu {
    width: 100%;
    max-width: 1212px;
    margin: 0 auto;
    padding: 16px 26px 0 32px;
    display: block;
  }

  .main-nav__logo {
    width: 165px;
    text-decoration: none;
    margin: 4px 64px 0 0;

    svg {
      width: 165px;
      height: 20px;
    }
  }

  .main-nav__toggle {
    width: 24px;
    height: 24px;
    margin: 0 22px 0 0;

    &--close {
      margin: -8px 22px 0 -3px;
      background-image: url("../img/menu-close-icon.svg");
    }
  }

  .main-nav__city {
    display: none;
  }

  .main-nav__help-list {
    width: 117px;
    justify-content: flex-start;

    &--menu {
      margin: 32px 0 0;
      width: 305px;
      justify-content: space-between;
    }
  }

  .main-nav__help-item {
    text-align: left;

    &--account {
      margin: 0;
    }

    &--address,
    &--tel,
    &--feedback {
      display: none;
    }
  }

  .main-nav__help-item-icon {
    width: 24px;
    margin: 3px 8px 0 0;

    svg {
      width: 24px;
      height: 24px;
    }

    &--big {
      width: 32px;
      margin: 3px 10px 0 0;

      svg {
        width: 32px;
        height: 32px;
      }

    }
  }

  .main-nav__help-item-description {
    width: 83px;
    font-size: 12px;
    line-height: 14px;

    &--menu {
      width: 150px;
      font-size: 16px;
      line-height: 19px;
    }

    &--white {
      width: 117px;

      & span {
        width: 83px;
      }

      &:before {
        width: 24px;
        height: 24px;
        margin: 0 8px 0 0;
      }
    }
  }

  .main-nav-menu {
    width: 100%;
    max-width: $tablet-max-width;
    margin: 0 auto;
    padding: 13px 0 22px;
    display: block;
  }

  .main-nav-menu__menu,
  .main-nav-menu__help-list,
  .main-nav-menu__help-item-description {
    display: flex;
    justify-content: left;
    align-items: center;
  }

  .main-nav-menu__menu {
    justify-content: space-between;
  }

  .main-nav-menu__logo {
    display: none;
  }

  .main-nav-menu__city {
    color: $blue;
    text-decoration: none;

    &:hover,
    &:focus {
      color: $hover-text;
    }

    &--white {
      color: $white;
    }
  }

  .main-nav-menu__help-list {
    width: 504px;
    list-style: none;
    margin: 32px 5px 0 0;
    padding: 0;
    justify-content: flex-start;

    &--company {
      margin: 0;
    }
  }

  .main-nav-menu__help-item {
    text-align: left;

    a {
      text-decoration: none;
    }

    &--address {
      margin: 0 80px 0 0;
    }

    &--account {
      margin: 0 0;
    }

    &--button {
      display: none;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .main-nav {
    padding: 18px 24px 0 24px;
  }

  .main-nav__wrapper {
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 21px;
    background-image: none;
  }

  .main-nav__logo {
    width: 99px;
    margin: 4px 37px 0 0;

    svg {
      width: 99px;
      height: 12px;
    }

    &--white {
      margin: 4px 20px 0 0;

      svg {
        fill: $blue;
      }
    }
  }

  .main-nav__menu {
    &--mobile {
      display: block;
      text-align: left;
    }
  }

  .main-nav__city {
    margin: 22px 0 0;
    font-size: 16px;
    line-height: 19px;
  }

  .main-nav__toggle {
    margin: 0 12px 0 0;

    &--white {
      background-image: url("../img/menu-icon.svg");
    }
  }

  .main-nav__help-list {
    width: 107px;

    &--menu {
      margin: 32px 0 0;
      width: 272px;
      display: block;
    }
  }

  .main-nav__help-item {
    text-align: left;

    &--address {
      margin: 0 0 10px 0;
    }

    &--account {
      width: 107px;
      margin: 0;
    }

    &--white {
      a {
        color: $blue;
      }
    }
  }

  .main-nav__help-item-icon {
    width: 24px;
    margin: 3px 8px 0 0;

    svg {
      width: 24px;
      height: 24px;
    }

    &--big {
      width: 32px;
      margin: 3px 10px 0 0;

      svg {
        width: 32px;
        height: 32px;
      }

    }
  }

  .main-nav__help-item-description {
    width: 83px;

    &:first-of-type {
      width: 55px;
    }

    &--menu {
      width: 200px;

      &:first-of-type {
        width: 200px;
      }
    }

    &--mobile {
      &:first-of-type {
        width: 107px;
      }

      width: 107px;

      & .main-nav__help-item-icon {
        display: none;
      }

      & span {
        display: flex;
        justify-content: flex-start;
        width: 77px;
      }

      &::before {
        display: block;
        content: "";
        width: 24px;
        height: 24px;
        background-image: url("../img/lk_white-icon.svg");
        background-repeat: no-repeat;
        background-size: cover;
        margin: 0 6px 0 0;
      }
    }
  }

  .main-nav-menu {
    max-width: $mobile-max-width;
    padding: 15px 0 0;
  }

  .main-nav-menu__toggle {
    width: 30px;
    height: 30px;
  }

  .main-nav-menu__menu {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .main-nav-menu__city {
    margin: 28px 0 0 0;
    font-size: 16px;
    line-height: 19px;
  }

  .main-nav-menu__help-list {
    width: 504px;
    list-style: none;
    margin: 30px 0 0 0;
    padding: 0;
    display: block;

    &--company {
      margin: 0;
    }
  }

  .main-nav-menu__help-item {
    &--address {
      margin: 0;
    }

    &--account {
      margin: 12px 0 0;
    }
  }

  .main-nav-menu__help-item-description {
    &--address {
      width: unset;
    }

    width: unset;
  }
}
